@import url(https://fonts.googleapis.com/css?family=Chelsea+Market&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header {
  text-align: center;
}
h1 {
  padding: 0.1em 1em;
  font-weight: bold;
  color: #FFF;/*文字色*/
  background: rgb(170, 0, 0);
  border: solid 3px rgb(0, 0, 0);/*線*/
  border-radius: 10px;/*角の丸み*/
  font-family: 'Chelsea Market', ;
}

.search{
  padding: 10px 0;
}

.MR {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media screen and (min-width:781px){
  .search {
    display: grid;
    grid-template-columns:1fr 2fr 3fr 1fr 3fr 1fr;
    height: 90%
  }



  h1{
    text-align: left;
    text-align: initial;
    grid-column: 1/2;
    font-family: 'Chelsea Market';
  }

  .search > .TextField{
    grid-column: 3/4;
  }
  select {
    font-size: 150%
  }

  p{
    padding:0 10px;
    margin: 0px;
  }

  .label p{
    text-align: center;
    font-size: 120%
  }


  .Sorting {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding:10px 0;
  }

  .Before {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding:10px 0;
  }

  .After {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding:0 0 10px;
  }
}

@media screen and (max-width:780px){
  .search {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns:1fr 3fr 1fr 1fr;
  }

  h1 {
    display: block;
    text-align: center;
    grid-row: 1/2;
    grid-column: 1/5;
    font-family: 'Chelsea Market';
  }


  .search > .TextField{
    grid-row: 2/3;
    grid-column: 2/3;
  }

  .search > .Button{
    grid-row: 2/3;
    grid-column: 3/4;
  }

  .MR p{
    margin: 0 1em 0 0 ;
    font-size: 120%
  }


  .Sorting p{
    margin: 5px;
    text-align: center;
    font-size: 120%
  }

  .Sorting select{
    padding: 6px;
    margin: 0 0 10px;
  }

  .label p{
    text-align: center;
    font-size: 120%
  }

  .Before {
    display: grid;
    grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;
    padding:0 0 20px
  }

  .Before select {
    height: 40px;
    padding:10px 0;
    margin: 10px 0;
    grid-column: 2/4;
  }
  .Before p {
    padding: 10px ;
    margin: 5px 0;
    grid-column: 5/7;
    font-size: 120%
  }
  .After {
    display: grid;
    grid-template-columns:1fr 1fr 1fr 1fr 1fr 1fr;
  }

  .After select {
    height: 40px;
    padding:10px 0;
    margin: 10px 0;
    grid-column: 2/4;
  }

  .After p {
    padding: 10px ;
    margin: 5px 0;
    grid-column: 5/7;
    font-size: 120%
  }


}

h5 {
  text-align: left;
  margin:0;
  word-break: break-all;
}

@media screen and (min-width:781px){
  .oneItem{
    width: 320px;
    margin: 1em;
    position: relative;
    padding: 30px;
    background-color: #e8ddbd; /*枠背景色*/
    box-shadow: inset 0 0 40px rgba(204, 186, 136,1), 0 2px 2px #ccc;
    /* margin: 1em 1em 1em 0 ; */
    /* padding: 0.5em 1em;
    margin: 2em 0; */
  }
  .oneItem::before {
    display: block;
    position: absolute;
    content: "";
    width: 140px;
    height: 35px;
    left: 35%;
    top: -16px;
    background-color: rgba(204, 186, 136,0.5);/*テープ色*/
    box-shadow: 0 0 3px rgba(0,0,0,0.1);
    -webkit-transform: rotate( -3deg );
            transform: rotate( -3deg ); /*テープ角度*/
  }

  .imges{
    width: 100px
  }

  .oneItem img {
    zoom: 80%
  }

  .oneItem h5{
    margin-top: 10px;
    font-weight:normal;
    /* font-size: 90%; */
  }

}


@media screen and (max-width:780px){
  .oneItem{
    display: grid;
    grid-template-columns:1fr 5fr;
    margin: 1em 0 ;
    /* padding: 0.5em 1em;
    margin: 2em 0; */
    background: #f0f7ff;
    border: dashed 2px #5b8bd0;/*点線*/
  }

  .oneItem img{
    zoom:50%

  }

  .oneItem h5{
    font-weight:normal;
    font-size: 85%;
    margin-left: 1em;
  }
}


